import React, { Component, useEffect, useRef, useState } from 'react'
import { AppBackButton, AppLabel, AppStatusLabel, AppTeamStatus } from '../../components/AppComponents'
import { AppDataTable } from '../../components/AppDataTable'
import { AppDataElement } from '../../components/AppDataElement'
import Main from '../Main';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPhone, faPlusCircle, faPlusSquare, faUserFriends } from '@fortawesome/free-solid-svg-icons'
import { Link, Redirect, useHistory } from 'react-router-dom';
import Button from '../../components/Button';
import { getData } from '../../lib/AppHelper';


export const Employees = () => {

   const [type, setType] = useState()
   const [permissions, setPermission] = useState({
      reseller_add: false
   })

   const DataTable = useRef()
   const MessageRef = useRef()
   useEffect(() => {
      (async () => {
         const per = await getData('permissions');
         setPermission(per);

      })()
   }, [])


   const history = useHistory()
   return (
      <Main MessageRef={MessageRef}>
         <div className="grid md:grid-cols-2">
            <div><AppBackButton to="/">Back to Dashboard</AppBackButton></div>
            {permissions.reseller_add ?
               <div>
                  <Link to='add-employee'>
                     <button title="Add Team Member" className="float-right bg-transparent hover:bg-red-500 text-red-700 font-semibold hover:text-white py-1 px-3 border border-red-500 hover:border-transparent rounded">
                        <FontAwesomeIcon icon={faPlusSquare}></FontAwesomeIcon>
                     </button>

                  </Link>

               </div>
               : false}
         </div>



         <AppDataElement MessageRef={MessageRef} endpoint={'members'} header={[]} render={(item, onClick) => {
            return <>
               <div onClick={() => {
                  if (item.team_cnt > 0) {
                     const path = "/employees/" + item.id
                     history.push(path)
                     onClick(item.id)
                  }

               }
               }
                  style={{ cursor: 'pointer' }}
               >
                  <div className="max-w-sm rounded overflow-hidden shadow-lg">
                     <div className="px-6 py-4">
                        <div className="font-bold text-xl mb-1 capitalize">{item.name}
                           {/* <span className="ml-2 text-xs text-gray-400 ">Level</span> */}

                           <span className="float-right text-sm text-primary">
                              <FontAwesomeIcon icon={faUserFriends} /> {item.team_cnt}  </span>
                        </div>
                     </div>
                     <div className="px-3 pt-4 pb-2">

                        <AppLabel title='CFO' color={'orange'} />
                        <a href={'tel:' + item.mobile} className="float-right text-sm text-primary">
                           <FontAwesomeIcon icon={faPhone} /> {item.mobile}  </a>
                        {/* <AppLabel title='Level' color={'yellow'} /> */}
                        <span className="float-right">
                           {item.status != 1 ?
                              <AppTeamStatus status={item.status} />
                              : ''}
                        </span>

                     </div>
                  </div>
               </div>
            </>
         }
         } params={{ type: type }} filters={<>
         </>} />

      </Main >
   )


}


// componentWillReceiveProps(nextProps) {

//    if (nextProps.location !== this.props.location) {
//       this.setState({ prevPath: this.props.location })
//    }
// }

export default Employees
