import React, { Component } from "react";
import { connect } from "react-redux";
import { Link, withRouter } from "react-router-dom";
import { AppCard, AppHeading, AppRow } from "../components/AppComponents";
import Button from "../components/Button";
import ProductsList from "../components/home/ProductsList";
import { AppStore } from "../lib/AppStore";
import Main from "./Main";
import insurance from "./../insurance.svg";
import shield from "./../shield.svg";
import { AiqaIcon } from "../components/AppIcons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Search } from "./Search";
import {
	faPlus,
	faRupeeSign,
	faUser,
	faUserAlt,
	faUserAstronaut,
	faUserFriends,
} from "@fortawesome/free-solid-svg-icons";
import { ApiGet } from "../lib/AppHelper";

/* import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css"; */

export class Home extends Component {
	MessageRef = React.createRef();
	state = {
		counts: {},
	};

	render() {
		const { user } = this.props;
		const { permissions } = user;
		const { counts } = this.state;

		return (
			<Main MessageRef={this.MessageRef}>
				<AppRow>
					<div className="flex-auto">
						<div className="max-w-3xl">
							{/* <Search /> */}

							<AppRow>
								<div className="flex-1 pt-10">
									<AppHeading className="mb-6">Hello, {user.name}</AppHeading>

									<AppCard title={false} className="mb-6">
										<div className="flex justify-between">
											<img alt="" src={insurance} />
											<div className="flex flex-col justify-between">
												<AiqaIcon size={30} className="text-primary" />

												{/* {permissions['application_add'] ?

													<Link to="/subscribe"><Button size="sm" display="inline-block" title="Add New Member" className="px-6" /></Link>
													: ''} */}

												<Link to="/subscribe">
													<Button
														size="sm"
														display="inline-block"
														title="Add New Member"
														className="px-6"
													/>
												</Link>
											</div>
											<img alt="" src={shield} />
										</div>
									</AppCard>
								</div>
							</AppRow>
							<div className="grid md:grid-cols-3 grid-cols-2 gap-4">
								<div className="col-span-1">
									<Link to="/subscriptions">
										<AppCard className="cursor-pointer" title="Active Members">
											<AppHeading color="primary">
												{/* <FontAwesomeIcon icon={faUserAstronaut} /> {user.active_subscription} */}
												<FontAwesomeIcon icon={faUserAstronaut} />{" "}
												{counts.sol_pol_amont}
											</AppHeading>
										</AppCard>
									</Link>
								</div>

								{user.type != 4 ? (
									<div className="col-span-1">
										<Link to="/leads">
											<AppCard className="cursor-pointer" title="Leads">
												<AppHeading color="primary">
													{/* <FontAwesomeIcon icon={faUserAstronaut} /> {user.pending_subscription} */}
													<FontAwesomeIcon icon={faUserAstronaut} />{" "}
													{counts.unsold_pol_cnt}
												</AppHeading>
											</AppCard>
										</Link>
									</div>
								) : (
									""
								)}

								<div>
									<AppCard className="cursor-pointer" title="Total Sales">
										<AppHeading color="primary">
											{/* <FontAwesomeIcon icon={faRupeeSign} />	{user.subscriptions_count} */}
											<FontAwesomeIcon icon={faRupeeSign} />{" "}
											{counts.sales_amont}
										</AppHeading>
									</AppCard>
								</div>

								{/* <div className="flex-1">
									{user.type === 2 ? <div className="flex-1">
										<Link to="/members">
											<AppCard className="cursor-pointer" title="Your Team">
												<AppHeading color="primary">
													<FontAwesomeIcon icon={faUserFriends} />  {user.members_count}
												</AppHeading>

											</AppCard>
										</Link>
									</div> : <div className="flex-1">
										<AppCard title="Total Sales">
											<AppHeading color="primary">$ 48,000</AppHeading>
										</AppCard>
									</div>}
								</div> */}
							</div>

							<AppRow>
								<div className="flex"></div>
								<div className="flex">
									{/* <Link to="/subscriptions">
										<AppCard className="cursor-pointer" title="Leads">
											<AppHeading color="primary">
												<FontAwesomeIcon icon={faUserAstronaut} /> {user.pending_subscription}</AppHeading>
										</AppCard>
									</Link> */}
								</div>
								{/* <div className="flex-1">
									<AppCard className="cursor-pointer" title="Total sales">
										<AppHeading color="primary">
											<FontAwesomeIcon icon={faRupeeSign} />	{user.subscriptions_count}</AppHeading>
									</AppCard>
								</div> */}
								{user.type === 2 ? (
									<div className="flex-1">
										{/* <Link to="/members">
										<AppCard className="cursor-pointer" title="View Teams">
											<AppHeading color="primary">
												<FontAwesomeIcon icon={faUserFriends} />  {user.members_count}



											</AppHeading>


										</AppCard>
									</Link> */}
									</div>
								) : (
									<div className="flex-1">
										{/* <AppCard title="Total Sales">
										<AppHeading color="primary">$ 48,000</AppHeading>
									</AppCard> */}
									</div >)}
							</AppRow>

							<AppHeading className="mb-6 mt-6" size="xl" font="normal">Our Products</AppHeading>

							<ProductsList />
						</div >
					</div >
					<div className="flex-2">
						<div className="grid md:grid-cols-1 grid-cols-2 gap-2">
							{/* <div className="p-4 rounded-xl shadow w-60">
                  </div>
                )}
              </AppRow>

              <AppHeading className="mb-6 mt-6" size="xl" font="normal">
                Our Products
              </AppHeading>

              <ProductsList />
            </div>
          </div>
          <div className="flex-2">
            <div className="grid grid-cols-1">
              {/* <div className="p-4 rounded-xl shadow w-60">
								<p className="font-semibold mb-3">Any Queries</p>
								<p className="text-xs mb-3">Lorem ipsum dolor sit amet consectetur.</p>
								<Button outline={true} title="Connect Now">
									<ChatIcon width={22} className="mr-2" />
								</Button>
							</div> */}
							{/* 
							{user.type === 2 || (permissions['employee_add']) ?
								<div className="col-span-1 mt-5 bg-gray-50 p-4 rounded-xl shadow ">
									<p className="font-semibold mb-3">Add Team</p>
									<p className="text-xs mb-3">Add an employee/reseller in your team.</p>
									<Link to="/add-employee"><Button outline={true} title="Add Now">
									</Button></Link>
								</div>
								: false} */}
							{user.type != 4 ?
								<Link to="/employees">
									<div className="col-span-1 mt-3 w-400-px" style={{ width: 200 }}>
										<AppCard className="cursor-pointer" title="Your Team">

											<AppHeading color="primary">
												<FontAwesomeIcon icon={faUserFriends} /> {counts.no_of_reportee}
											</AppHeading>
										</AppCard>

										{/* <p className="text-xs mb-3">Add an employee/reseller in your team.</p> */}

									</div> </Link> : false}

							{user.type != 4 ?
								<div className="col-span-1 mt-3">
									<Link to="/claims">
										<AppCard className="cursor-pointer" title="Claim">
											<AppHeading color="primary">
												<FontAwesomeIcon icon={faRupeeSign} /> {user.active_subscription}</AppHeading>
										</AppCard>
									</Link>
								</div> : false}
							{/* 
							{user.type === 2 || (permissions['employee_add']) ?
								<div className="pt-4 col-span-1">
									<Link to="/members">
										<AppCard className="cursor-pointer" title="Your Team">
											<AppHeading color="primary">
												<FontAwesomeIcon icon={faUserFriends} /> {user.pending_subscription}</AppHeading>

										</AppCard>
									</Link>

								</div>
								: false} */}
							{/* 
							<div className="pt-4 col-span-1">
								<Link to="/members">
									<AppCard className="cursor-pointer" title="Add Team">
										<AppHeading color="primary">
											<Link to="/add-employee"><Button outline={true} title="Add Now">
											</Button></Link> </AppHeading>
									</AppCard>
								</Link>

							</div> */}
						</div>
					</div>
				</AppRow >
			</Main >
		);
	}

	componentDidMount() {
		AppStore.dispatch({ type: "PROGRESS", progress: 0 });
		this.getCounts();
	}

	getCounts = async () => {
		AppStore.dispatch({ type: "LOADING", loading: true });

		// console.log("props=", queryString.parse(this.props.location.search))

		const response = await ApiGet("counts");

		if (response.status === "success") {
			this.setState({ counts: response.data });

			// console.log("@@@@@@@@@@@", response.data);
		} else {
			this.MessageRef.current.show(response.message);
		}

		AppStore.dispatch({ type: "LOADING", loading: false });
	};
}

const mapStateToProps = (state) => {
	const { loading, user } = state;
	return { loading: loading, user: user };
};

const HomeConnect = connect(mapStateToProps)((props) => {
	return <Home {...props} />;
});

export default withRouter(HomeConnect);
